"use client";

import { WalletConnect } from "@/components/Wallet-connect";
import { useState } from "react";
import { useContract } from "@/hooks/useContract"; // Import the hook
import SampleIntegration from "@/components/sample";

// Let's assume we have a list of heroes
const heroes = ["Axe", "Invoker", "Pudge", "Juggernaut", "Lion"];

export default function Home() {
	const [isPlaying, setIsPlaying] = useState(false);
	const [score, setScore] = useState(0);
	const [heroToGuess, setHeroToGuess] = useState("");
	const [userGuess, setUserGuess] = useState("");
	const [message, setMessage] = useState("Start the game to play!");

	const { contractData, isLoading, isPending, isConfirming, isConfirmed, error, setValue } = useContract();

	const startGame = () => {
		setIsPlaying(true);
		setScore(0);
		setUserGuess("");
		setNextHero();
		setMessage("Guess the hero!");
	};

	const setNextHero = () => {
		const randomIndex = Math.floor(Math.random() * heroes.length);
		setHeroToGuess(heroes[randomIndex]);
	};

	const handleGuess = () => {
		if (!userGuess) return;

		if (userGuess.toLowerCase() === heroToGuess.toLowerCase()) {
			setScore(score + 1);
			setMessage(`Correct! It was ${heroToGuess}. Now guess the next one!`);
		} else {
			setMessage(`Wrong! The correct hero was ${heroToGuess}. Try the next one!`);
		}
		setUserGuess("");
		setNextHero();
	};

	const stopGame = async () => {
		setIsPlaying(false);
		setMessage(`Game over! Your final score is ${score}. Saving score to the contract...`);
		
		try {
			// Call the contract to set the value
			await setValue(score);
			setMessage(`Game over! Your final score is ${score}. Score saved!`);
		} catch (e) {
			console.error("Failed to set value on contract", e);
			setMessage("Could not save your score to the contract. See console for details.");
		}
	};

	return (
		<div style={{ padding: "2rem", fontFamily: "sans-serif", textAlign: "center" }}>
			<h1>Hero Guessing Game</h1>
			<WalletConnect />
			
			{/* We add the SampleIntegration back to handle object ID */}
			<div style={{ margin: "1rem 0" }}>
				<SampleIntegration />
			</div>

			<div style={{ marginTop: "2rem", padding: "2rem", border: "1px solid #ccc", borderRadius: "8px" }}>
				{!isPlaying ? (
					<div>
						<p style={{ fontStyle: "italic", minHeight: "24px" }}>{message}</p>
						<button onClick={startGame} style={{ fontSize: "1.2rem", padding: "0.8rem 1.5rem", cursor: "pointer" }}>
							Start Game
						</button>
					</div>
				) : (
					<div>
						<h2>Score: {score}</h2>
						<p style={{ fontStyle: "italic", minHeight: "24px" }}>{message}</p>
						<p>What hero am I thinking of?</p>
						<input
							type="text"
							value={userGuess}
							onChange={(e) => setUserGuess(e.target.value)}
							onKeyPress={(e) => e.key === "Enter" && handleGuess()}
							style={{ fontSize: "1rem", padding: "0.5rem" }}
							placeholder="Enter hero name"
						/>
						<button onClick={handleGuess} style={{ fontSize: "1rem", padding: "0.5rem 1rem", marginLeft: "0.5rem", cursor: "pointer" }}>
							Guess
						</button>
						<button onClick={stopGame} style={{ fontSize: "1rem", padding: "0.5rem 1rem", marginLeft: "0.5rem", backgroundColor: "#f44336", color: "white", border: "none", cursor: "pointer" }} disabled={isPending || isConfirming}>
							{isPending || isConfirming ? "Saving..." : "Stop Game"}
						</button>
					</div>
				)}

				{/* Display transaction status */}
				<div style={{ marginTop: "1rem" }}>
					{isPending && <p>Transaction is pending...</p>}
					{isConfirming && <p>Transaction is confirming...</p>}
					{isConfirmed && <p>Transaction confirmed!</p>}
					{error && <p style={{ color: "red" }}>Error: {error}</p>}
				</div>
			</div>
		</div>
	);
}
